#foter{
  width: 100%;
  height: 200px;
  background-color: black;
  opacity: 0.7;
  position: relative;
  margin-top: 10%; 
}
#foter>ul{
  position: relative;
  width: 100%;
  top: 50px;
  display: -webkit-flex;
  display: flex;
  margin-left: -20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-direction: row;
          flex-direction: row;
}
#foter>ul>li{
  list-style: none;
}
#foter>ul>li>a{
  font-size: 1.5em;
  color: white;
  
}
#foter>ul>li>a>span{
  margin-left: 25%;
}

/*query*/
@media(max-width: 767px){
#foter>ul>li>a{
  font-size: 1.3em;
  padding: 15px;
}
}
@media(min-width:1025px){
  #foter>ul>li>a{
  font-size: 2em; 
  }
}
@media(min-width:769px){
   #foter>ul>li>a{
  font-size: 1.8em; 
  }
}



